import loginApi from "../api/login";

const actions = {
  /**
   * 利用vuex管理登录状态
   */
  login(context, params) {
    return new Promise((resolve, reject) => {
      loginApi
        .login(params)
        .then(res => {
          if (res.success) {
            let token = res.data.userid;
            let rememberMe = params.rememberMe || false;
            context.commit("login", token, rememberMe);
            resolve(res);
          } else {
            reject(res)
          }
        })
        .catch(() => {});
    });
  },
  logout(context) {
    return new Promise((resolve, reject) => {
      loginApi
        .logout()
        .then(res => {
          if (res.success) {
            context.commit("logout");
            resolve(res);
          } else {
            reject(res);
          }
        })
        .catch(err => {
          reject(err);
        });
    });
  }
};

export default actions;
