import axios from "axios";
import qs from "qs";
import store from "../store/index";
import router from "../router/index";
import {Message} from "view-design";

const TIME_OUT_MS = 30 * 1000; // 默认请求超时时间
const BASE_URL = "/api"; //baseUrl
let loading;

let http = axios.create({
  //baseURL: BASE_URL,
  timeout: TIME_OUT_MS,
  //withCredentials: true,
  headers: {
    // "Accept": "application/json; charset=utf-8",
    // 'Content-Type': 'application/json;charset=UTF-8'
    "Content-Type": "application/x-www-form-urlencoded; charset=UTF-8"
  }
});

/**
 * 请求拦截器
 */
http.interceptors.request.use(
  config => {
    config.emulateJSON= true;
    config.baseURL = store.getters.contextPath + BASE_URL;
    // console.log('---------store:'+store.getters);

    if (store.getters.isLogin) {
      // config.headers.Authorization = "Bearer " + store.getters.token;
      config.headers.common['token']=store.getters.token;
    }

    //config.headers["client-ip"] = ipBase['cip'];
    config.cancelToken = new axios.CancelToken(cancel => {
      store.commit("pushReq", cancel);
    });
    if (config["unload"]){
      config.headers.unload = config["unload"];
    }
    if (config.method === "post") {
      if (config.data && config.data.unload){
        config.headers.unload = config.data.unload;
      }

      //console.log('----------'+);
      if(config.headers["Content-Type"]==='application/x-www-form-urlencoded; charset=UTF-8'){
        config.data = qs.stringify(config.data);
      }
    }
    if (!config.headers.unload) {
      //加载动画
      // loading = Toast.loading({
      //   duration: 0, // 持续展示 toast
      //   message: '加载中...',
      //   forbidClick: true,
      //   loadingType: 'spinner'
      // });

      if(loading){
          //先关闭其他加载框
          Message.destroy();
      }
      loading =  Message.loading({
            duration:0, //持续展示
            content:'加载中...',
        })


    }
    return config;
  },
  error => {
    return Promise.reject(error);
  }
);

const toLogin = () => {
  router.replace({
    path: "/Login",
    query: {
      redirect: router.currentRoute.fullPath
    }
  });
};

const errorHandle = err => {
  let {status, data} = err;

  let {message} =
    typeof data === "string"
      ? {message: data}
      : data || {message: "系统错误"};
  switch (status) {
    // 401: 未登录
    case 401:

      // 清除token
      store.commit("logout");
      setTimeout(() => {
        toLogin();
      }, 500);
      break;
    // 403 token过期
    case 403:

      break;
    // 404请求不存在
    case 404:

      break;
    // 其他错误，直接抛出错误提示
    default:
        this.$Message.error({
            content:message,
            duration:5
        })
  }
};

/**
 * 响应拦截器
 */
http.interceptors.response.use(
  // 请求成功
  res => {
    if (loading) {
      //loading.clear();
        Message.destroy();
    }
    //let rToken = res.headers["refresh-token"];
    //if (rToken) {
    //  store.commit("updateToken", rToken);
    //}
    //过滤token过期的响应
    if(res.status===200){
      if(res.data.code===-2){
        //alert('token已过期，请重新登录');
        store.commit("logout");
        toLogin();
      }
    }
    return res.status === 200 ? Promise.resolve(res.data) : Promise.reject(res);
  },
  // 请求失败
  error => {
    // loading.clear();
      Message.destroy();
    if (error.response) {
      /*if (error.response.status) {
                errorHandle(error.response.status, error.response.data.message);
            }*/
      let unHandle = false;
      const unHandleErr = () => {
        unHandle = true;
      };
      setTimeout(() => {
        if (!unHandle) {
          errorHandle(error.response);
        }
      });
      return Promise.reject({...error.response, unHandleErr});
    }
  }
);

export default http;
