import Vue from 'vue'
import Vuex from "vuex";
import actions from "../store/action";

// Vue.use(Vuex);
Vue.use(Vuex);

const store = new Vuex.Store({
  state: {
    locale: window.localStorage.getItem("locale") || "",
    visitkey:window.sessionStorage.getItem("visitkey") ||
        window.localStorage.getItem("visitkey") ||
        "",
    token:
        window.sessionStorage.getItem("web_token") ||
        window.localStorage.getItem("web_token") ||
        "",
    contextPath: process.env.NODE_ENV === "production" ? "" : "",
    processRequests: [],
    userdata: window.sessionStorage.getItem("userdata") ||
        window.localStorage.getItem("userdata") ||
        "",
    browser: "",
    isLogin: false,
    rememberMe: false,
    title: window.document.title,
  },
  getters: {
    locale: state => state.locale,
    visitkey:visitkey=>state.visitkey,
    token: state => state.token,
    userdata:state => state.userdata,
    contextPath: state => state.contextPath,
    browser: state => state.browser,
    isLogin: state => {
      return state.token.length > 2;
    }
  },
  mutations: {
    reLocale(state, lang) {
      state.locale = lang;
      window.localStorage.setItem("locale", lang);
    },
    pushReq(state, req) {
      state.processRequests.push(req);
    },
    abortRequest({ processRequests }) {
      processRequests.forEach(cancel => {
        cancel(); //取消上个页面所有请求
      });
    },
    updateVisitkey(state,visitkey){
      state.visitkey = visitkey;
    },
    updateUserdata(state, userdata) {

      if (state.rememberMe) {
        window.localStorage.setItem("userdata", userdata);
      } else {
        window.sessionStorage.setItem("userdata", userdata);
      }
      state.userdata = userdata;
    },
    updateToken(state, token) {
      //alert('upD:'+state.rememberMe);
      if (state.rememberMe) {
        window.localStorage.setItem("web_token", token);
      } else {
        window.sessionStorage.setItem("web_token", token);
      }
      state.token = token;
    },
    login(state, userdata) {

      state.rememberMe = userdata.rememberMe;
      state.isLogin = true;

      //token
      //alert(userdata.rememberMe);
      let userdat = JSON.parse(userdata.data);
      if (userdata.rememberMe) {
        window.localStorage.setItem("web_token", userdat.token);
      } else {
        window.sessionStorage.setItem("web_token", userdat.token);
      }
      state.token = userdat.token;

      //userdata
      if (userdata.rememberMe) {
        window.localStorage.setItem("userdata", userdata.data);
      } else {
        window.sessionStorage.setItem("userdata", userdata.data);
      }
      state.userdata = userdata.data;

    },
    logout(state) {
      window.localStorage.removeItem("web_token");
      window.sessionStorage.removeItem("web_token");
      window.sessionStorage.removeItem("userdata");
      window.localStorage.removeItem("userdata");
      state.token = "";
      state.userdata = "";
      state.isLogin = false;
    },
    updateTitle(state, title){
      window.document.title = title;
      state.title = title || window.document.title;
    }
  },
  actions
});

export default store;
